import d3 from 'd3';
import ShapeGenerator from 'core/shape-generator.js';

export default class LineGenerator extends ShapeGenerator {
  constructor(chart, options = {}) {
    let {
      interpolation = 'step'
    } = options;
    super(
      d3.svg.line().interpolate(interpolation)
        .x(function(datum) { return chart.axes.x.scale()(chart.accessor.x(datum)); })
        .y(function(datum) { return chart.axes.y.scale()(chart.accessor.y(datum)); })
    );
  }
  draw(chart) {
    this.$el = chart.$el.selectAll('.series')
      .data(chart.series)
      .enter()
      .append('g')
      .attr('class', 'series')
      .attr('clip-path', `url(#clip-path:${chart.id})`);
    this.$el.append('path')
      .attr('class', 'line')
      .attr('d', function(s) {
        // xx
        return chart.generator.gen(s.data);
      })
      .style('stroke', function(s) { return chart.color(s.name); });
    return this;
  }
  refresh(chart) {
    this.$el.data(chart.series).select('path.line')
      .attr('d', function(s) { return chart.generator.gen(s.data); });
    return this;
  }
}


