import d3 from 'd3';

export function draggable(el, listeners) {
  return d3.select(el).call(
    d3.behavior.drag().on('dragstart', listeners.dragstart)
      .on('drag', listeners.drag)
      .on('dragend', listeners.dragend)
  );
}

export class Zoomer {
  constructor(chart, orient = {}) {
    let {
      x = true,
      y = false
    } = orient;
    this.$el = d3.select('__empty__');
    this.chart = chart;
    this.orient = {
      x: x,
      y: y
    };
    this.zoom = d3.behavior.zoom().on('zoom', function() {
      if (x) {
        chart.axes.x.$el.call(chart.axes.x);
      }
      if (y) {
        chart.axes.y.$el.call(chart.axes.y);
      }
      if (chart.ruler) {
        chart.ruler.stick();
      }
      chart.generator.refresh(chart);
    }).on('zoomstart', function() {
      d3.select(this).classed('zooming', true);
    }).on('zoomend', function() {
      d3.select(this).classed('zooming', false);
    });
    this.bindAxes();
  }
  bind(el) {
    this.$el = d3.select(el)
      .classed('zoomable', true)
      .call(this.zoom);
    if (this.orient.x) {
      this.$el.classed('x', true);
    }
    if (this.orient.y) {
      this.$el.classed('y', true);
    }
  }
  // Update axes's scale while zooming
  // If the scale's domain or range is modified programmatically, this function should be called again
  bindAxes() {
    if (this.orient.x) {
      this.zoom.x(this.chart.axes.x.scale());
    }
    if (this.orient.y) {
      this.zoom.y(this.chart.axes.y.scale());
    }
    return this;
  }
}


