import d3 from 'd3';

export default class ShapeGenerator {
  constructor(generator) {
    this.generator = generator;
    this.$el = d3.select('__empty__');
  }
  draw(/*chart*/) {}
  refresh(/*chart*/) {}
  gen(data) {
    return this.generator(data);
  }
}


