import d3 from 'd3';
import { evalOpt, mergeObj, uuid } from 'helpers/utils.js';
import 'extension/selection.js';
import LineGenerator from 'core/line-generator.js';
import { Zoomer } from 'core/behavior.js';
import Ruler from 'core/ruler.js';

const DEFAULTS_CHART_HEIGHT = 500;
const DEFAULTS_CHART_WIDTH = 900;
const DEFAULTS_CHART_MARGIN_TOP = 20;
const DEFAULTS_CHART_MARGIN_BOTTOM = 30;
const DEFAULTS_CHART_MARGIN_LEFT = 60;
const DEFAULTS_CHART_MARGIN_RIGHT = 20;

function axis(type, range, orient, domain) {
  let scale = type === 'time' ? d3.time.scale() : d3.scale[type]();
  return d3.svg.axis().scale(scale.range(range).domain(domain)).orient(orient);
}

function extent(series, accessor) {
  return [
    d3.min(series, function(s) { return d3.min(s.data, accessor); }),
    d3.max(series, function(s) { return d3.max(s.data, accessor); })
  ];
}

function color(series) {
  return d3.scale.category20().domain(d3.map(series, function(s) {
    return s.name;
  }));
}

function evalAxes({ x, y, width, height }, series, accessor) {
  return {
    x: mergeObj(evalOpt(x, function(width, height, series, accessor, options) {
      return axis(
        options.type,
        [0, width],
        options.position || 'bottom',
        evalOpt(options.domain, extent, series, accessor)
      );
    }, width, height, series, accessor.x), x, {
      $el: d3.select('__empty__')
    }),
    y: mergeObj(evalOpt(y, function(width, height, series, accessor, options) {
      return axis(
        options.type,
        [height, 0],
        options.position || 'left',
        evalOpt(options.domain, extent, series, accessor)
      );
    }, width, height, series, accessor.y), y, {
      $el: d3.select('__empty__')
    })
  };
}

export default class Chart {
  constructor(accessor, series = [], options = {}) {
    let {
      width = DEFAULTS_CHART_WIDTH - DEFAULTS_CHART_MARGIN_RIGHT - DEFAULTS_CHART_MARGIN_LEFT,
      height = DEFAULTS_CHART_HEIGHT - DEFAULTS_CHART_MARGIN_TOP - DEFAULTS_CHART_MARGIN_BOTTOM,
      margin = {
        top: DEFAULTS_CHART_MARGIN_TOP,
        right: DEFAULTS_CHART_MARGIN_RIGHT,
        bottom: DEFAULTS_CHART_MARGIN_BOTTOM,
        left: DEFAULTS_CHART_MARGIN_LEFT
      },
      // legend,
      // title = '',
      axes = {
        x: {
          type: 'time',
          position: 'bottom' // or 'top'
        },
        y: {
          type: 'linear',
          position: 'left' // or 'right'
        }
      },
      zoomer = {
        x: true,
        y: false
      },
      ruler = {
        mode: 'hover',
        orient: 'vertical'
      },
      generator
    } = options;
    this.id = uuid();
    this.$el = d3.select('__empty__');
    this.width = width;
    this.height = height;
    this.margin = margin;
    this.series = series;
    this.color = color(series);
    this.accessor = accessor;
    this.axes = evalAxes(mergeObj(axes, {
      width: width,
      height: height
    }), series, accessor);
    if (zoomer) {
      this.zoomer = new Zoomer(this, zoomer);
    }
    if (ruler) {
      this.ruler = new Ruler(this, ruler);
    }
    this.generator = generator || new LineGenerator(this);
  }
  draw(el/*, parent = el*/) {
    // draw svg context and clip path
    this.$el = d3.select(el).append('svg')
      .attr('version', 1.1)
      .attr('xmlns', 'http://www.w3.org/2000/svg')
      .attr('xmlns:xlink', 'http://www.w3.org/1999/xlink')
      .attr('viewBox', [
        0,
        0,
        this.width + this.margin.left + this.margin.right,
        this.height + this.margin.top + this.margin.bottom
      ].join(' '))
      .attr('width', this.width + this.margin.left + this.margin.right)
      .attr('height', this.height + this.margin.top + this.margin.bottom)
      .append('g')
      .attr('transform', 'translate(' + this.margin.left + ',' + this.margin.top + ')');
    this.$el.append('defs')
     .append('clipPath')
      .attr('id', `clip-path:${this.id}`)
     .append('rect')
      .attr('x', 0)
      .attr('y', 0)
      .attr('width', this.width)
      .attr('height', this.height);
    this.axes.x.$el = this.$el.append('g')
      .attr('class', 'x axis')
      .attr('transform', function() {
        if (this.axes.x.position === 'top') {
          return 'translate(0, 0)';
        }
        return 'translate(0, ' + this.height + ')';
      }.bind(this))
      .call(this.axes.x);
    this.axes.y.$el = this.$el.append('g')
      .attr('class', 'y axis')
      .attr('transform', function() {
        if (this.axes.y.position === 'right') {
          return 'translate(' + this.width + ', 0)';
        }
        return null;
      }.bind(this))
      .call(this.axes.y);
    this.$el.append('rect')
          .attr('class', 'interactive-panel')
          .attr('width', this.width)
          .attr('height', this.height);
    this.generator.draw(this);
    if (this.ruler) {
      this.ruler.draw().bind().move(
        this.ruler.orient === 'vertical' ? this.width / 2 : this.height / 2
      );
    }
    // make it zoomable
    if (this.zoomer) {
      this.zoomer.bind(
        this.$el.select('.interactive-panel').node()
      );
    }
    return this;
  }
  refresh() {
    this.color = color(this.series);
    // TODO
    return this;
  }
  delegate(el, event, callback) {
    let chart = this;
    d3.select(el).on(event, function(d, i) {
      callback.call(this, chart, d, i);
    });
    return this;
  }
}


